.wrapper {
    width: 100%;
    background: rgb(255, 255, 255);
height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    overflow: auto;
    background: #1c92d2;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #f2fcfe, #1c92d2);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(#f2fcfe), to(#1c92d2));
    background: -webkit-linear-gradient(top, #f2fcfe, #1c92d2);
    background: -o-linear-gradient(top, #f2fcfe, #1c92d2);
    background: linear-gradient(to bottom, #f2fcfe, #1c92d2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    

    
    
    
  }


